import request from '../../common/request.js'
export default {
	data() {
		return {
			recommendList:[],//推荐商品
			user_type:2
		}
	},
	created() {
		this.res();
	},
	methods: {
		//跳转设置列表
		navSetup() {
			uni.navigateTo({
				url: '/pages/mine/mineSetup'
			})
		},
		// 获取推荐商品数据
		res() {
			var data = {};
			request.post('index/index', data).then(res => {
				this.recommendList = res.data.data_list
			})
		},
		//跳转详情页
		navToDetailPage(id) {
			uni.navigateTo({
				url: '/pages/productDetails/productDetails?goods_id='+id
			})
		},
	}
}
